.accordion {
	width: 80%;
	margin: 0 auto 15px !important;
	background-color: #efefef;
	margin-bottom: 15px;
	&__fullWidth {
		width: 100%;
	}
	p {
		color: #676767;
	}
	&__details {
		border-top: 1px solid #000;
		flex-direction: column;
	}
	&__content {
		div {
			display: flex;
			justify-content: space-between;
			align-items: center;
		}
	}
	&__deleteIcon {
		color: red;
		&:hover {
			transform: scale(1.2);
		}
	}
}
